<template>
  <v-dialog v-model="dialog" max-width="1000px">
    <v-card class="full">
      <v-card-title>
        <span class="text-h6">{{ title }} 선택</span>
      </v-card-title>
      <div class="container">
        <v-data-table
          v-model="selectedList"
          fixed-header
          :headers="headers"
          :items="list"
          :items-per-page="-1"
          :item-key="tableKey"
          show-select
        >
          <template v-slot:[`item.admin_yn`]="{ item, header }">
            <v-select
              v-model="item[header.value]"
              :items="selectItems[header.value]"
              placeholder="admin"
              :disabled="header.disabled"
              dense
              outlined
              class="caption"
              hide-details="auto"
              item-text="label"
              item-value="state"
              style="width: 200px; margin: 0 auto"
            ></v-select>
          </template>

          <template v-slot:[`item.role_master_name`]="{ item, header }">
            <v-select
              v-model="item[selectKeys[header.api[1]].modelKey]"
              :items="selectItems[header.api[1]]"
              placeholder="shiptype"
              :disabled="header.disabled"
              dense
              outlined
              class="caption"
              hide-details="auto"
              :item-text="header.textKey"
              :item-value="header.valueKey"
              style="width: 200px; margin: 0 auto"
            ></v-select>
          </template>
        </v-data-table>
      </div>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
        <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
import Vue, { ref, watch, reactive, nextTick } from "vue";
import { getRequest, postRequest } from "@/services/request.js";
import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "DialogCreateItem",
  props: ["payload"],
  setup(props) {
    const authStore = useAuthStore();

    //
    let PAYLOAD = null;
    let parentListSet = null;
    const tableKey = ref("");
    const selectKeys = reactive({});
    const selectItems = reactive({});
    const selectedList = ref([]);
    const title = ref("");
    const headers = ref([]);
    const list = ref([]);

    const dialog = ref(false);
    const close = () => {
      dialog.value = false;
      nextTick(() => {
        selectedList.value = [];
      });
    };

    const save = () => {
      const { secoundHeaders, parentItem } = PAYLOAD;

      const list = selectedList.value.reduce((result, item) => {
        const returnObj = {};

        if (parentListSet.has(item[tableKey.value])) {
          //   //
          returnObj.saveType = "update";
          const preItem = parentListSet.get(item[tableKey.value]);

          secoundHeaders.map((header) => {
            if (header.useUpdateItem === true) {
              returnObj[header.modelKey || header.value] =
                item[header.modelKey || header.value] ||
                preItem[header.modelKey || header.value] ||
                null;
            }
          });
        } else {
          returnObj.saveType = "new";
          secoundHeaders.map((header) => {
            if (header.useUpdateItem === true) {
              returnObj[header.modelKey || header.value] =
                item[header.modelKey || header.value] || null;
            }
          });
        }

        result.push(returnObj);
        return result;
      }, []);

      const body = {
        ...parentItem,
        list,
      };

      const [fileName, apiName] = PAYLOAD.api;
      postRequest([fileName, apiName], body, (returnList) => {
        PAYLOAD.setList(returnList);
      });
      close();
    };

    watch(
      () => props.payload,
      (payload) => {
        PAYLOAD = payload;
        tableKey.value = payload.secoundHeaders[0].value;

        const newSelectedList = [];
        payload.list.map((item) => {
          if (payload.targetListMap.has(item[tableKey.value])) {
            newSelectedList.push(item);
          }
        });

        selectedList.value = newSelectedList;

        const promises = [];
        const newHeaders = payload.headers.filter((item) => {
          if (Object.prototype.hasOwnProperty.call(item, "type")) {
            if (item.type === "select") {
              if (
                Object.prototype.hasOwnProperty.call(item, "role") &&
                item.role.indexOf(authStore.userInfo.role) === -1
              ) {
                item.disabled = true;
              } else {
                item.disabled = false;
              }

              if (Object.prototype.hasOwnProperty.call(item, "options")) {
                const defaultValue = item.options[0].state;
                payload.list.map((li) => {
                  if (payload.targetListMap.has(li[tableKey.value])) {
                    Vue.set(
                      li,
                      item.value,
                      payload.targetListMap.get(li[tableKey.value])[item.value]
                    );
                  } else if (
                    !Object.prototype.hasOwnProperty.call(li, item.value)
                  ) {
                    Vue.set(li, item.value, defaultValue);
                  }
                });

                selectItems[item.value] = item.options;
              } else {
                const [fileName, apiName] = item.api;
                promises.push(
                  new Promise((res) => {
                    getRequest(item.api, null, (items) => {
                      const defaultValue = items[0][item.modelKey];

                      payload.list.map((li) => {
                        if (payload.targetListMap.has(li[tableKey.value])) {
                          Vue.set(
                            li,
                            item.modelKey,
                            payload.targetListMap.get(li[tableKey.value])[
                              item.modelKey
                            ]
                          );
                          // li[item.modelKey] = payload.targetListMap.get(
                          //   li[tableKey.value]
                          // )[item.modelKey];
                        } else if (li[item.modelKey] === -1) {
                          Vue.set(li, item.modelKey, defaultValue);
                          // li[item.modelKey] = defaultValue;
                        }
                      });

                      selectKeys[apiName] = {
                        textKey: item.textKey,
                        valueKey: item.valueKey,
                        modelKey: item.modelKey,
                      };

                      selectItems[apiName] = items;

                      res(true);
                    });
                  })
                );
              }
            }
            return true;
          }
          return false;
        });

        Promise.all(promises).then(() => {
          title.value = payload.title;
          headers.value = newHeaders;
          list.value = payload.list;
          parentListSet = new Map(
            PAYLOAD.targetList.map((item) => {
              return [item[tableKey.value], item];
            })
          );

          dialog.value = true;
        });
      }
    );

    return {
      selectKeys,
      selectItems,
      tableKey,
      selectedList,
      title,
      headers,
      list,

      dialog,
      close,
      save,
    };
  },
};
</script>
